import Circle from "ol/geom/Circle";
import LineString from 'ol/geom/LineString';
import Feature from "ol/Feature";
import Map from "ol/Map";
import View from "ol/View";
import { Stroke, Style } from "ol/style";
import { OSM, Vector as VectorSource } from "ol/source";
import { Tile as TileLayer, Vector as VectorLayer } from "ol/layer";
import {fromLonLat} from 'ol/proj';
import {getDistance} from 'ol/sphere';

import Stamen from 'ol/source/Stamen';

const styles = {
  Done: new Style({
    stroke: new Stroke({
      color: "green",
      width: 4
    })
  }),
  NotDone: new Style({
    stroke: new Stroke({
      color: [255, 0, 0, 0.7],
      width: 4
    })
  }),
};

const styleFunction = function (feature) {
  return styles[feature.get("name")];
};


const vectorSource = new VectorSource();

let doneLineString = new LineString([]);
let notDoneLineString = new LineString([]);

import tracks from './tracks.json';
console.log("track length: " +  tracks["features"][0]["geometry"]["coordinates"][0].length)
let meters = 0;
//let completed = 190740;
let completed = 233820;

for(let p = 0; p < tracks["features"][0]["geometry"]["coordinates"][0].length; p++) {
    curCoord = tracks["features"][0]["geometry"]["coordinates"][0][p];
    if(p > 0)
    {
        prevCoord = tracks["features"][0]["geometry"]["coordinates"][0][p-1];
        meters += getDistance(prevCoord, curCoord);
    }
    if(meters < completed)
    {
        doneLineString.appendCoordinate(fromLonLat(curCoord));
    }
    else
    {
        notDoneLineString.appendCoordinate(fromLonLat(curCoord));
    }
}
vectorSource.addFeature(new Feature({
    geometry: doneLineString,
    name: "Done"
}));
vectorSource.addFeature(new Feature({
    geometry: notDoneLineString,
    name: "NotDone"
}));
console.log("Meters: " + meters + " Completed " + completed)

const vectorLayer = new VectorLayer({
  source: vectorSource,
  style: styleFunction
});

const map = new Map({
  layers: [
    new TileLayer({
      source: new Stamen({
        layer: 'watercolor',
      }),
    }),
      /*
    new TileLayer({
      source: new Stamen({
        layer: 'terrain-labels',
      }),
    }),
    */
    vectorLayer
  ],
  target: "map",
  view: new View({
    center: fromLonLat([-3, 54.51]),
    zoom: 5
  })
});

